import { ethers } from "ethers";
import { formatFixed, parseFixed } from '@ethersproject/bignumber'

// const evalInScope = async (js, contextAsScope) => {
//     return async () => { with(this) { return await eval(js); }; }.call(contextAsScope);
// }

const utils = {
  formatFixed,
  parseFixed,
}

const prefix = "return (async (ethers, utils) => {"
const subfix = "})(ethers, utils)"

export const compileCode = (code) => {
  let finalCode = prefix + code + subfix;
  try {
    return new Function("ethers", "utils", finalCode);
  } catch (e) {
    return undefined;
  }
}

export const runCode = async (code) => {
  let f = compileCode(code);
  try {
    return await f(ethers, utils);
  } catch (e) {
    return undefined;
  }
}
